@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap');

$bp-medium: 61.25em; // 980px
$bp-smallest: 31.25em; // 500px
$Very-Dark-Blue: hsl(235, 21%, 11%);
$Very-Dark-Desaturated-Blue: hsl(235, 24%, 19%);
$Light-Grayish-Blue: hsl(234, 39%, 85%);
$Very-Dark-Grayish-Blue: hsl(237, 14%, 26%);
$Light-Grayish-Blue-hover: hsl(236, 33%, 92%);
$Dark-Grayish-Blue: hsl(234, 11%, 52%);
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;

  @media only screen and (max-width: $bp-medium) {
    font-size: 50%;
  }
}

body {
  font-family: 'Josefin Sans', sans-serif;
}

.body-background-color {
  background-color: $Very-Dark-Blue;
}

.attribution {
  text-align: center;
  margin-top: 5rem;
  font-size: 2rem;
  color: $Very-Dark-Desaturated-Blue;
}

.first-container {
  height: 42vh;
  background-image: url('../images/bg-desktop-dark.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (max-width: 550px) {
    background-image: url('../images/bg-mobile-dark.jpg');
  }
}

.light-image {
  background-image: url('../images/bg-desktop-light.jpg');

  @media (max-width: 550px) {
    background-image: url('../images/bg-mobile-light.jpg');
  }
}

.todo-container {
  padding-top: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 35%;
  margin-left: auto;
  margin-right: auto;

  @media (max-width: 1100px) {
    width: 40%;
  }

  @media (max-width: 800px) {
    width: 60%;
  }

  @media (max-width: 800px) {
    width: 92%;
  }
  &-heading {
    color: white;
    font-size: 3rem;
    letter-spacing: 5px;
    font-weight: 700;
  }
}

.form {
  width: 35%;
  border-radius: 2px;
  margin: 0 auto;

  @media (max-width: 1100px) {
    width: 40%;
  }

  @media (max-width: 800px) {
    width: 60%;
  }

  @media (max-width: 550px) {
    width: 92%;
  }

  &_input {
    font-size: 1.5rem;
    font-family: inherit;
    padding: 1.5rem 5rem;
    border-radius: 5px;

    width: 100%;
    border: none;
    display: inline-block;
    height: 5.2rem;
    transition: all 0.2s ease-out;

    &-background {
      background-color: $Very-Dark-Desaturated-Blue;
      color: white;
    }

    &:focus {
      outline: none;
      border-bottom: 3px solid $Light-Grayish-Blue;
    }

    &:focus:invalid {
      border-bottom: 3px solid white;
    }
  }
}

.circle-svg {
  transform: translateX(1.5rem) translateY(4rem);
  color: $Light-Grayish-Blue;
  font-weight: 200;
  height: 2rem;
  width: 2rem;
}

.box {
  width: 35%;

  margin: 1.5rem auto;
  padding: 1rem;
  border-radius: 5px;
  padding-bottom: 2rem;

  @media (max-width: 1100px) {
    width: 40%;
  }

  @media (max-width: 800px) {
    width: 60%;
  }
  @media (max-width: 550px) {
    width: 92%;
  }
}

.color-light {
  background-color: white;
  color: hsl(235, 19%, 35%);
}

.color-dark {
  background-color: $Very-Dark-Desaturated-Blue;
  color: $Light-Grayish-Blue;
}

.item {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-top: 2rem;

  &-clear-completed {
    word-spacing: -1.1px;
  }
}

.font {
  font-size: 1.5rem;
  color: $Dark-Grayish-Blue;

  &:hover {
    cursor: pointer;
    color: $Light-Grayish-Blue-hover;
  }
}

.new-item {
  display: flex;
  align-items: center;
  padding: 1rem;

  & svg {
    margin-left: auto;
    cursor: pointer;
    opacity: 1;
  }
}

.heading {
  margin-left: 2rem;
  font-weight: 300;
}

.hr {
  height: 0.1px;
  display: block;
  width: 100%;
  background-color: hsl(233, 13%, 45%);
  margin: 1rem 0;
}

.hr-light {
  background-color: hsl(236, 33%, 92%);
}

input[type='checkbox'] {
  display: none;
}

label {
  position: relative;
  display: block;
  border: 1px solid hsl(237, 14%, 26%);
  height: 20px;
  width: 20px;
  border-radius: 50%;
  transform: translateX(-3px);
}

label:hover {
  cursor: pointer;
  border: 1px solid hsl(220, 98%, 61%);
}

label::after {
  content: '';
  position: absolute;
  background-image: url(../images/icon-check.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 4px;
  width: 4px;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  transform: rotateZ(15deg) scale(10) translateY(3px);
  //display: none;
}

input[type='checkbox']:checked + label::after {
  opacity: 1;
  transform: translate(-2px, -2px) rotateZ(15deg) scale(1);
  background-color: blue;
  height: 26px;
  width: 26px;
  display: block;
}

input[type='checkbox']:checked ~ h1 {
  text-decoration: line-through;
  color: hsl(235, 19%, 35%);
}

// .addLine {
//   text-decoration: line-through;
// }

.active {
  color: hsl(220, 98%, 61%);
}
